
.post-wrap{display:block;margin-bottom:15px;max-width:500px;border:3px solid #eee
}
.post-wrap input{padding:5px 15px;margin-top:10px;font-weight:bold;font-size:18px;width:calc(100% - 60px)
}
.post-wrap .title{padding:5px 15px;font-weight:bold;text-transform:uppercase;text-align:center
}
.post-wrap .descr{padding:15px;text-align:left
}
.post-wrap button{border-radius:0;width:140px;height:30px;text-transform:uppercase;text-align:center;margin-bottom:15px;margin-right:10px
}
.loader{position:fixed;left:0;top:0;background-color:rgba(0,0,0,0.7);width:100%;height:100%
}
.loader .text{position:absolute;top:50%;left:50%;margin-left:-50px;color:#fff
}



